import React from 'react';
import MenuIcon from '@mui/icons-material/Menu';
import SearchIcon from '@mui/icons-material/Search';
import NotificationsIcon from '@mui/icons-material/Notifications';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import LanguageIcon from '@mui/icons-material/Language';
import Badge from '@mui/material/Badge';
import IconButton from '@mui/material/IconButton';

export default function Header() {
  return (
    <header className="flex items-center justify-between bg-gray-800 text-white p-4">
      {/* Left Side: Menu Icon and Search Bar */}
      <div className="flex items-center space-x-4">
        {/* Menu Icon */}
        <IconButton edge="start" color="inherit" aria-label="menu">
          <MenuIcon />
        </IconButton>

        {/* Search Bar */}
        <div className="flex items-center bg-gray-700 px-2 py-1 rounded-md">
          <SearchIcon />
          <input
            type="text"
            placeholder="Search..."
            className="bg-transparent text-white px-2 focus:outline-none"
          />
        </div>
      </div>

      {/* Right Side: Notifications, Language Selector, User Profile */}
      <div className="flex items-center space-x-6">
        {/* Notifications */}
        <IconButton color="inherit">
          <Badge badgeContent={3} color="error">
            <NotificationsIcon />
          </Badge>
        </IconButton>

        {/* Language Selector */}
        <div className="flex items-center space-x-2">
          <LanguageIcon />
          <select
            className="bg-gray-700 text-white p-2 rounded-md focus:outline-none"
            defaultValue="EN"
          >
            <option value="EN">EN</option>
            <option value="FR">FR</option>
            <option value="ES">ES</option>
          </select>
        </div>

        {/* User Profile */}
        <IconButton color="inherit">
          <AccountCircleIcon fontSize="large" />
        </IconButton>
      </div>
    </header>
  );
}
